
import axios from 'axios';
import {Products} from '../constants/routes';

const headers = {
    'Accept': 'application/json'
};

export function viewCollections(uuid){
   
    return  dispatch => {
       
     axios.get("http://orch.eastus.cloudapp.azure.com:8080/send_friend?uuid="+uuid)
     .then(function (response) {
       
          let  obj = {
              status : response.status,
              data : response.data
          }
          return dispatch({ 
              type : "ViewCollections_Result", 
              payload :obj
             } )
         
          })
        .catch(function (error) {
            return dispatch({ type : "Checkout_Error", payload : error } )
         });
         
     }
  } 




