var checkout_initialstate ={
    cart:{}
 }
 export default function(state=checkout_initialstate,action){
     
     if(action.type === "Checkout_Result"){
         return{
             ...state,
             cart:action.payload    
         }
     }
     if(action.type === "Checkout_Error"){
        return{
            ...state,
            //products:action.payload    
        }
    }
    
    
     return state;
 }


