import React, {Component} from 'react';
import Nav from './nav';
import Sliderr from './slider';

class Home extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
     }
  componentDidMount() {
      
    if (typeof(Storage) !== "undefined") {
        console.log(localStorage.getItem("uuid"))
        if(localStorage.getItem("uuid")=="" || localStorage.getItem("uuid")==null)
           this.props.history.push('/signin');

   }
}
    render() {
        return (
           <div>

               <Nav/>
               <div className="card" style={{margin:"7vh",marginTop:"15vh"}}>

                    <div className="card-body">
                        <h4 className="card-title">Customized</h4>
                        <Sliderr type="desire"/>
                    </div>

                </div>

                <div className="card" style={{margin:"7vh"}}>

                    <div className="card-body">
                        <h4 className="card-title">Trending<i className="fa fa-chart-line"></i></h4>
                        <Sliderr type="inspire"/>
                    </div>

                </div>




           </div>
        );
    }
}



export default (Home);



