import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as myactions from '../../action_creators/products';
import Print from './print'
class TextChat extends Component {
    constructor(props){
        super(props);
        this.state = {
          answer : "Sorry, I don't know!",
          question:"Adi",
          productid : props.productid,
          userid :  props.userid,
          tmp: []
          
        }
     }
    componentDidMount() {
       // console.log("Prodid-Text-Did"+this.state.productid)
        console.log("Prodid-Text-Did"+sessionStorage.upid)
        this.props.chatbot(this.props.steps.question.message,sessionStorage.upid,this.state.userid);
        this.props.triggerNextStep({ value:null, trigger:"question" })
       
    }
   
    componentWillReceiveProps(nextProps) {
      
          if (nextProps.products) {
          //console.log(nextProps.products.chatResponse.response)
            this.setState({
                
                tmp:[...this.state.tmp,nextProps.products.chatResponse.answer]
             
            })
           // this.props.triggerNextStep({ value:null, trigger:"question" })
            
          }
          else{
            this.setState({
                
                tmp:[...this.state.tmp,this.state.answer]
             
            })
          }
      }
    handleChat(question){

    }

    answer(){
        let length = this.state.tmp.length;
        console.log("tmp"+length);
        console.log(this.state.tmp)
       
    }
    
    render() {
        console.log(this.state.tmp)
        return (
           <div>
               
            {/*<Print answer={this.state.answer}/>*/} 
            { 
              
               this.state.tmp.map(function(item,index){
                   if(index == 2)
                   return (
                    <div>   
                    <p key={index}>{item}</p>    
                   
                    </div>
                   )
                   
               },this)

            }
           </div>
        );
    }
}



function mapStateToProps(state){
    
      return{
         
         products : state.products
         
      }
  }
  function matchDispatchToProps(dispatch){
      
      return bindActionCreators(myactions,dispatch)
    
      
  }

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(TextChat));



