var sharedcollections_initialstate ={
    sharedcollections:{},
    feedbackResponse:{}
 }
 export default function(state=sharedcollections_initialstate,action){
     
     if(action.type === "SharedCollections_Result"){
         return{
             ...state,
             sharedcollections:action.payload    
         }
     }
     if(action.type === "SharedCollections_Error"){
        return{
            ...state,
            //products:action.payload    
        }
    }
    if(action.type === "Feedback_Result"){
        return{
            ...state,
            feedbackResponse:action.payload    
        }
    }
    
     return state;
 }


