import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as myactions from '../action_creators/viewcollections';
import AlertContainer from 'react-alert';
import Nav from './nav';
import Lightbox from 'lightbox-react';
import ViewCollection from './viewcollection'


class Collections extends Component {
    constructor(props){
        super(props);
        this.state = {
            
            photoIndex: 1,
            isOpen: false,
            images : [],
            collections:{},
            collectionskeys:[],
            userid:localStorage.uuid
        }
     }
    componentDidMount() {

        this.props.viewCollections(this.state.userid);
      
    }
    componentWillReceiveProps(nextProps) {
    
   
          if (nextProps.viewcollections) {
            console.log(nextProps.viewcollections.viewcollections)
            if(nextProps.viewcollections.viewcollections.status == 200){
                let keys = Object.keys(nextProps.viewcollections.viewcollections.data.response);
                let data = nextProps.viewcollections.viewcollections.data.response;
                let images = keys.map((item,index)=><ViewCollection key = {item.toString()} data={data[item]} />)
                this.setState({
                    collections:data,
                    collectionskeys:keys,
                    images : images
                })
            }
          
          }
      }
   
    displayCollections(item,index){
       
        let imageUrl = this.state.collections[item][0].info.image;
        return (
                   
                    <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12">
                   
                                    <div className="custcontainer" 
                                    style={{ height: '300px',overflow:"hidden",
                                    marginBottom:'5vh' ,cursor:"pointer"}}>
                                   
                                    <div className="view overlay"  onClick={() => this.setState({ isOpen: true,photoIndex:index })}>
                                        <img src={imageUrl}
                                       
                                        className="img-fluid z-depth-1" alt="placeholder"/>
                                        <div className="mask flex-center waves-effect waves-light rgba-black-light">
                                            <p className="white-text"> <i className="fa fa-heart fa-1x" 
                                            aria-hidden="true"></i> <b style={{paddingLeft:"3px"}}>5</b>
                                            <i class="fa fa-comment fa-1x" style={{paddingLeft:"30px"}} aria-hidden="true"></i>
                                            <b style={{paddingLeft:"5px"}}>5</b></p>
                                        </div>
                                      
                                    </div>
                                  
                                                
                            
                                 
                                   
                                           
                    </div>
                    <p style={{textAlign:"center"}}><b>collectionname</b></p>   
                    </div>
                   
          
                   
                  
             
        )   
     }
  
    render() {
        const {
            photoIndex,
            isOpen,
            images
        } = this.state;
        return (
        <div>
           <AlertContainer ref={a => this.msg = a} {...this.alertOptions} />
                <Nav/>
                
                <div style={{
                    marginLeft:"5vw",
                    marginTop:"10vh",
                    marginBottom:"10vh",
                    marginRight:"5vw"}}>
                <div className="row" style={{overflow:"scroll"}}>
                {this.state.collectionskeys.map(this.displayCollections,this)}
                </div>
                {isOpen &&
                    <Lightbox
                        enableZoom = {false} 
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}

                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() => this.setState({
                            photoIndex: (photoIndex + images.length - 1) % images.length,
                        })}
                        onMoveNextRequest={() => this.setState({
                            photoIndex: (photoIndex + 1) % images.length,
                        })}
                    />
                }
               
                </div>
        </div>
        );
    }
}



function mapStateToProps(state){
    
      return{
         
        viewcollections : state.viewcollections
         
      }
  }
  function matchDispatchToProps(dispatch){
      
      return bindActionCreators(myactions,dispatch)
    
      
  }

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(Collections));



