import React, {Component} from 'react';

class Print extends Component {
    constructor(props){
        super(props);
        this.state = {
          answer:props.answer
          
        }
     }
     componentDidMount() {
       
        console.log("PPPPP"+this.props.answer)
      
     }
    
    render() {
        return (
           <div> 
                <p>{this.props.answer}</p>
           </div>
        );
    }
}
export default Print;


