import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as myactions from '../action_creators/sharedcollections';
import Nav from './nav_ns';
import ReactTooltip from 'react-tooltip'
import '../css/sharedcollection.css'
import AlertContainer from 'react-alert'
const queryString = require('query-string');

class SharedCollections extends Component {
    constructor(props){
        super(props);
        this.state = {
           
            products:[],
            shareid:"",
            shareuuid:"",
            like:[]

        }
     }
     alertOptions = {
        offset: 14,
        position: 'top center',
        theme: 'dark',
        time: 5000,
        transition: 'scale'
      }
     errorshowAlert = (msg) => {
        this.msg.show(msg, {
          time: 5000,
          type: 'success',
         // icon: <img src={require('../images/error.png')} />
        })
      }
     successshowAlert = (msg) => {
        this.msg.show(msg, {
          time: 5000,
          type: 'success',
         // icon: <img src={require('../images/success.png')} />
        })
      }

    componentDidMount() {
        let parsed = queryString.parse(this.props.location.search);
        let isshareid = Object.prototype.hasOwnProperty.call(parsed, 'shareid');
        let isshareuuid = Object.prototype.hasOwnProperty.call(parsed, 'shareuuid');

        if(isshareid && isshareuuid){ 
             
              this.setState({shareid:parsed.shareid, shareuuid:parsed.shareuuid})
         
              this.props.getCollections(parsed.shareid,parsed.shareuuid);
        }
        
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.collections.sharedcollections) {
            let newState = nextProps.collections.sharedcollections;
            if(newState!=null && newState!=undefined && !this.isEmpty(newState)){
                let status = newState.status;
               
                if(status==200){
                    let data = newState.data.response;
                    this.setState({
                        products:data.map((item,i)=>{
                            item.like=false
                            item.comment=""
                            return item
                        })
                    })

                }
               
            }
            if (nextProps.collections.feedbackResponse){
                if (!this.isEmpty(nextProps.collections.feedbackResponse)){
                    if(nextProps.collections.feedbackResponse.status==200){
                       
                        this.successshowAlert("Thanks for feedback.")
    
                    }
                   
                }
            }
          

          }
          if (nextProps.collections) {


          }  
          
          
      }
    isEmpty(obj) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }
    handlelike(index){
       
        let products = this.state.products.slice();
        products[index].like = !products[index].like;
        this.setState({
            products:products
        })
        
    }
    handleComment(e,index){
        let comment = e.target.value;
        let products = this.state.products.slice();
        products[index].comment = comment;
        this.setState({
            products:products
        })
     
    }
    handlefeedback(){

        let products = this.state.products;
        let votes = products.map((item,index)=>{
            let obj = {
               "upid":item.upid,
               "vote":item.like?"Y":"N",
               "comment":this.refs["commentbox"+index].value
            }
            return obj;
        })
        console.log(votes);
        let obj = {
                "shareid":this.state.shareid,
                "shareuid":this.state.shareuuid,
                "votes": votes
                 
        }
        
        this.props.feedback(obj);
        
    }
    displayProducts(item,index){
       
        return (
                   
                    <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12">
                    <div className="custcontainer">
                             <div className="card" key = {item.upid} style={{margin:'1vh',padding:"5px"}}>
                               
                                   
                                    <div className="custcontainer">
                                            
                                            <div class="view overlay zoom" style={{height: '300px',overflow:"hidden",cursor:"pointer"}}>
                                                <img style={{}} src={item.image_url}
                                                className="img-fluid rounded " alt={item.title}/>
                                             
                                                    <div className="mask flex-center waves-effect waves-light rounded">
                                                            <a className="btn btn-link"  target="_blank" href={item.prod_url}
                                                            style={{backgroundColor:"white",opacity:"0.6"}} >{item.like}Quick View</a>
                                                    </div> 
                                            </div>       
                                 
                                  
                                     </div>   
                                     <br/>
                                     <span  
                                                    class="heart"><i onClick={()=>this.handlelike(index)} 
                                                    className="fa fa-heart" 
                                                    style={{cursor: 'pointer',
                                                    color:(!item.like)?"white":"red",
                                                    WebkitTextStrokeWidth: "1px",
                                                    WebkitTextStrokeColor:"red"}}
                                                    aria-hidden="true"></i></span>
                                     <h6 style={{fontSize:"12px",fontWeight:"",color:"#757575"}}className="card-title"><b>{item.title}</b></h6>
                                     <h6 style={{fontSize:"12px",fontWeight:"600",color:"#757575"}} className="card-text">{item.store}</h6>
                                     <div className="row">
                                        <div className="col-7">
                                            <h5><b className="card-text">$ {item.price}</b></h5>
                                        </div>       
                                        <div className="col-5">
                                        <a className="badge badge-pill light-blue">BAG IT<i class="fa fa-plus ml-2"></i></a>
                                        </div>       
                                     </div>
                                    
                                     <textarea placeholder="comment" 
                                     className="form-control"  ref={"commentbox"+index}rows="1">
                                     </textarea>
                                     </div> 
                                           
                             </div>
                           
                    </div>
                   
 
                   
                  
             
        )   
     }
    render() {
        return (
           <div>
           <AlertContainer ref={a => this.msg = a} {...this.alertOptions} />
       
                <Nav/>
                <div style={{
                    marginLeft:"5vw",
                    marginTop:"10vh",
                    marginBottom:"10vh",
                    marginRight:"5vw"}}>
                <div className="row" style={{overflow:"scroll",height:"85vh"}}>
                {this.state.products.map(this.displayProducts,this)}
                </div>
                <div style={{position:"fixed",bottom:"5px",right:"5vw"}}>
                    <button onClick={()=>this.handlefeedback()}
                    className="btn btn-default">Feedback</button>
                </div>
                </div>
           </div>
        );
    }
}



function mapStateToProps(state){
    
      return{
         
         collections : state.sharedcollections
         
      }
  }
  function matchDispatchToProps(dispatch){
      
      return bindActionCreators(myactions,dispatch)
    
      
  }

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(SharedCollections));



