var sharedcollections_initialstate ={
    viewcollections:{}
 }
 export default function(state=sharedcollections_initialstate,action){
     
     if(action.type === "ViewCollections_Result"){
         return{
             ...state,
             viewcollections:action.payload    
         }
      }
     return state;
 }


