var profile_initialstate ={
   products:{},
   sendtoResponse:{},
   chatResponse:{},
   askResponse:{},
   cartResponse:{},
   loadcartResponse:{},
   deleteResponse:{},
   error:""
}
export default function(state=profile_initialstate,action){
    
    if(action.type === "Products_Result"){
        return{
            ...state,
            products:action.payload,
            error:""    
        }
    }
    if(action.type === "Search_Result"){
        return{
            ...state,
            products:action.payload,
            error:""    
        }
    }
    if(action.type === "Sendto_Result"){
        return{
            ...state,
            sendtoResponse:action.payload,
            error:""    
        }
    }
    if(action.type === "Chatbot_Result"){
        return{
            ...state,
            chatResponse:action.payload,
            error:""    
        }
    }

    if(action.type === "Ask_Result"){
        return{
            ...state,
            askResponse:action.payload,
            error:""    
        }
    }
    if(action.type === "Cart_Result"){
        return{
            ...state,
            cartResponse:action.payload ,
            loadcartResponse:{},
            error:""   
        }
    }
    if(action.type === "Load_Cart_Result"){
        return{
            ...state,
            loadcartResponse:action.payload,
            error:""    
        }
    }
    if(action.type === "Delete_Product_Result"){
        return{
            ...state,
            deleteResponse:action.payload,
            error:""    
        }
    }
    if(action.type === "System_Error"){
        return{
            ...state,
            error:"Something went wrong, Try again."    
        }
    }
   
    return state;
}


