import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import Nav from './nav'
import * as myactions from '../action_creators/products';

class Contactus extends Component {
    constructor(props){
        super(props);
        this.state = {
          products : "Development in process.",
        
        }
     }
    componentDidMount() {
      
    }
    componentWillReceiveProps(nextProps) {
          if (nextProps.products) {
        
            this.setState({
             
              products:"Development in process."
             
            })
          
          }
      }
    componentDidUpdate (){
       
    } 
    render() {
        return (
           <div>
                <Nav/>
                <iframe src="https://numentio.typeform.com/to/EMwtY9" width="100%" height="800px"></iframe>
                 </div>
        );
    }
}



function mapStateToProps(state){
    
      return{
         
         products : state.products
         
      }
  }
  function matchDispatchToProps(dispatch){
      
      return bindActionCreators(myactions,dispatch)
    
      
  }

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(Contactus));



