import React, {Component} from 'react';
import Nav from './nav';
import Images from './images';
import PSM from './psm';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as myactions from '../action_creators/products';
import AlertContainer from 'react-alert'
import ReactTooltip from 'react-tooltip'
import Loading from './spinner'
import '../css/products.css'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
var Spinner = require('react-spinkit');
const queryString = require('query-string');

class Products extends Component {
    constructor(props){
        super(props);
        this.state = {
          
          isSearch:false,

          isask:false, 
          isopenanswerpanel:false,  
          isLoadingAnswer:false,
          answerSet:{},

       
          ischeckout:false,
          isLoading:true,
         
          
          products:[],
          sendto:[],
          chatproduct:{},
          selectedProductId :"",
          cart :[],
          start:1,
          end:50,
          scroll:true,
          userid:localStorage.getItem("uuid")
        }
     }

     alertOptions = {
        offset: 14,
        position: 'top center',
        theme: 'dark',
        time: 5000,
        transition: 'scale'
      }
     errorshowAlert = (msg) => {
        this.msg.show(msg, {
          time: 5000,
          type: 'success',
         // icon: <img src={require('../images/error.png')} />
        })
      }
     successshowAlert = (msg) => {
        this.msg.show(msg, {
          time: 5000,
          type: 'success',
         // icon: <img src={require('../images/success.png')} />
        })
      }

    componentDidMount() {
    
      let parsed = queryString.parse(this.props.location.search);
      let isSearch = Object.prototype.hasOwnProperty.call(parsed, 'query');
      if(isSearch){ 
           
            this.setState({
                isSearch:true,
                isLoading:true
            })
       
           this.props.onSearch(parsed.query,localStorage.getItem("uuid"));
      }
      else{
        this.props.onLoad(localStorage.getItem("uuid"),this.state.start,this.state.end);
      }  
      if(!this.state.isSearch)
      document.getElementById("productwindow").addEventListener('scroll',this.handleScroll)
    }

    componentWillUnmount(){
        document.getElementById("productwindow").addEventListener('scroll',this.handleScroll)
    }

    handleScroll=()=>{
        console.log("Aditya")
        if(this.state.scroll){
            let {scrollHeight,scrollTop} = document.getElementById("productwindow");
            let newstart = this.state.start+50;
            let newend = this.state.end+50;
            if(scrollTop > scrollHeight - 2000){
                if(!this.state.isSearch)
                this.props.onLoad(localStorage.getItem("uuid"),newstart,newend);
                this.setState({
                    start : newstart,
                    end: newend,
                    scroll:false
                })
            }
            
        }
        
   
    }
    componentWillReceiveProps(nextProps) {
        
       
       

        if(this.state.isSearch){
           console.log("I was here")
            if (nextProps.products) {
                if(nextProps.products!=undefined && !this.isEmpty(nextProps.products)){
                   
                    let type = this.state.type; 
                    let category = this.state.category;
                    let items = nextProps.products.products.response;
                   
                    this.setState({
                     
                      products:items,
                   
                     
                    })
                }
                
              }
        }
        else{
            if (nextProps.products) {
             
                if(nextProps.products!=undefined && !this.isEmpty(nextProps.products)){
                
                    let prod = nextProps.products.products["desire"];
                    if(prod!=null && prod!=undefined){
                        var keys = Object.keys(prod)
                        var data = [];
                        keys.map((item,index)=>{
                        
                            (nextProps.products.products["desire"][item]).map((item,index)=>data.push(item));
                
                        });
                    
                        this.setState({
                        
                        products:this.state.products.concat(data),
                        scroll:true
                        
                        })
                    }
                }
              }
        }  

        if (nextProps.products.sendtoResponse){
            if(!this.isEmpty(nextProps.products.sendtoResponse)){
              
                if(nextProps.products.sendtoResponse.status == 200)
                    this.successshowAlert(nextProps.products.sendtoResponse.msg)
                else
                    this.errorshowAlert("something went wrong, Try later")    
            }

                
        }

        if (nextProps.products.cartResponse){
        
            if(!this.isEmpty(nextProps.products.cartResponse)){
        
                if(nextProps.products.cartResponse.status == 200){
                      if(this.state.ischeckout)
                      this.props.history.push('/checkout');
 
                   }
                }
                  
                
        }

        if (nextProps.products.loadcartResponse){
        
            if(!this.isEmpty(nextProps.products.loadcartResponse)){
        
                if(nextProps.products.loadcartResponse.status == 200){
                     
                      let data = nextProps.products.loadcartResponse.data.items;
                     
                      let store = [];
                      for(var key in data) store.push(key);
                     
                      var oldcart = []
                      store.map((s,i)=>data[s].map((selectedItem,index)=>{

                          let obj = {
                            upid:selectedItem.upid,
                            title:selectedItem.title,
                            image_url:selectedItem.image_url,
                            price:selectedItem.price,
                            colors:selectedItem.colors,
                            sizes:selectedItem.sizes,
                            selectedcolor : selectedItem.colors[0],
                            selectedsize : selectedItem.sizes[0],
                            selectedqty:1
                          }
                          oldcart.push(obj);

                      }))
                      
                      this.setState({
                         cart:oldcart
                      })
 
                   }
                }
                  
                this.setState({
                    isLoading:false
                })
        }        
        
        if (nextProps.products.askResponse){
        
            if(!this.isEmpty(nextProps.products.askResponse)){
                
                if(nextProps.products.askResponse.status == 200){
                    this.setState({
                        answerSet:nextProps.products.askResponse.data,
                        isLoadingAnswer:false
                    })
 
                   }
                }
                  
                
        }


        if (nextProps.products.deleteResponse){
        
            if(!this.isEmpty(nextProps.products.deleteResponse)){
                
                if(nextProps.products.deleteResponse.status == 200){
                    //this.successshowAlert("Item is deleted successfully.")
                    let index = nextProps.products.deleteResponse.index;
                    let newProd = this.state.products.slice();
                    newProd.splice(index,1);
                    this.setState({
                        products:newProd
                    })
                }else{
                    this.errorshowAlert("Item is not deleted, Try again.")
                }
              } 
            }
        
        if (nextProps.products.error){      
            if(nextProps.products.error!==""){

                this.errorshowAlert(nextProps.products.error);

            }    
        }
       
   
   
        }

    handleSendto(selectedItem){
      
        this.setState({
            sendto:[...this.state.sendto,selectedItem]
        })
        this.successshowAlert("Item Addded...");

    }
    handleAddtoCart(selectedItem){
       
         var obj = {
             upid:selectedItem.upid,
             title:selectedItem.title,
             image_url:selectedItem.image_url,
             price:selectedItem.price,
             colors:selectedItem.colors,
             sizes:selectedItem.sizes,
             selectedcolor : selectedItem.colors[0],
             selectedsize : selectedItem.sizes[0],
             selectedqty:1
         }

         
         let data = this.state.cart.slice();

         data.push(obj);
       

         this.setState({
             cart:[...this.state.cart,obj],
            
         })
         this.successshowAlert("Item Addded to cart...");
        
         this.handleCheckout("cart",data);
    }
    handleupdatecart(index,type,e){
      
        let cart  = this.state.cart.slice();
        let item = cart[index];

        if(type === "qty"){
            item.selectedqty =  e.target.value;
        }
        if(type === "color"){
            item.selectedcolor =  e.target.value;
        }
        if(type === "size"){
            item.selectedsize =  e.target.value;
        }
        this.setState({
            cart:cart
        })
       
    }
    handleCheckout(type,data=this.state.cart){
     
       
      
            let checkoutitems = data.map((item,index)=>{
                let obj = {
                    upid : item.upid,
                    qty : parseInt(item.selectedqty),
                    selectedsize: item.selectedsize,
                    selectedcolor:item.selectedcolor
                }
                return obj;
            })
           
            let checkout = {
                uuid : this.state.userid,
                items:checkoutitems
            }
        
            this.props.cart(checkout);
            if(type=="checkout"){
        
                this.setState({
                    ischeckout:true
                })
            } 
       
    }
    handleAddToCartDeleteItem(index){
       
        let cart = this.state.cart;
   
        cart.splice(index,1);
        
        this.setState({
            cart:cart
        })
        this.successshowAlert("Item removed..");
        this.handleCheckout("deletecartitem")
    }
    handleSendtoFriends(selectedItem){
       let uuid = localStorage.getItem("uuid");
       let uphones = this.refs.mobilenumbers.value;
       let uemails = this.refs.emails.value;
       let umsg = this.refs.msg.value;
       let cname = this.refs.collectionname.value;
       let upids = this.state.sendto.map((item,index)=>item.upid);
       upids = upids.join();
     
       let obj = {
        uuid:uuid,
        upids:upids,
        uphones:uphones,
        uemails:uemails,
        umsg:umsg,
        cname:cname
       }
       this.props.Sendto(obj);
       
    }
    handleSendToDeleteItem(index){
       
        let sendto = this.state.sendto;
   
        sendto.splice(index,1);
        
     
        this.setState({
            sendto:sendto
        })
        this.successshowAlert("Item removed..");
    }

   
   
    handleChat(selectedchatitem){
    
        this.setState({
            chatproduct:selectedchatitem,
            selectedChatProductId:selectedchatitem.upid
        })
        if(typeof(Storage) !== "undefined") {
            if (sessionStorage.upid) {
                sessionStorage.upid = selectedchatitem.upid;
            } else {
                sessionStorage.upid = selectedchatitem.upid;
            }
        }    
        

    }
  
    handleAsk = (e) => {
        if (e.key === 'Enter') {
          this.handleAskBtn();
        }
      
    }

    handleAskBtn(){
        
        let query  = this.refs.ask.value;
        let upid =""
        this.state.products.map(function(item,index){
            upid += item.upid +",";
        })
        let obj = {
            query : query+"?",
            upid : upid.slice(0, -1)
        }
        this.setState({
            isLoadingAnswer:true,
            isopenanswerpanel:true
        })
         
        this.props.Ask(obj);
    }
    handleDeleteProduct(item,index){
        let obj={
            uuid:this.state.userid,
            items:[item.upid]
        }
        this.props.deletefromcollection(obj,index)
    }
    handleAnswerSet(key){
        if(!this.isEmpty(this.state.answerSet.response)){
            if(this.state.answerSet.response[key]!=undefined){
                var ans = this.state.answerSet.response[key];
                var answer = "";
                if(ans.concept=="colors"){
                   if(ans.answer.length!=0){
                    return <div>
                                    { ans.answer.map((item,index)=>(<svg width="35" height="35">
                                <rect width="30" height="30" style={{fill:item.code,paddingRight:"20vw"}} /> 
                            </svg>))}
                        </div>  
                   }else{
                    return <div>
                    <p> { "Please Check The Product!"}</p>
                    
                    </div>
                   } 
                   
                   
             
                }else{
                    //ans.answer.map((item,index)=>answer+=item+", ")
                    answer = ans.answer;
                }
                return <div>
                    <p> { (answer=="")?"Please Check The Product!":answer}</p>
                    
                    </div>
            }
            else{
                return <div>
                    <p> {"Please Check The Product!"}</p>
                    
                    </div>
            }
        }
        return <p></p>
    }
    
   

    isEmpty(obj) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }

    displayProducts(item,index){
       return (
                
                   <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12">
                   
                            <div className="card" key = {item.upid} style={{margin:'1vh',padding:"5px"}}>
                              
                                    <div>
                                    <div className="custcontainer">
                                  
                                    <div class="view overlay zoom" style={{height: '300px',overflow:"hidden",cursor:"pointer"}}>
                                        <img style={{width:"100%",height: '300px',objectFit: "contain"}} src={item.image_url==""?require("../Images/notfound.png"):item.image_url}
                                        className="img-fluid rounded " alt={item.title}/>
                                       
                                        <div className="mask flex-center waves-effect waves-light rounded">
                                                <a className="btn btn-link"  target="_blank" href={item.prod_url}
                                                style={{backgroundColor:"white",opacity:"0.6"}} >Quick View</a>
                                                <a style={{display:this.state.isSearch?"None":""}}
                                                onClick={()=>this.handleDeleteProduct(item,index)} className="cross">&#10005;</a>
                                        </div> 
                                    </div>       
                                   
                                    </div>
                                    <br/>
                                    <h6 style={{fontSize:"12px",fontWeight:"",color:"#757575"}}className="card-title"><b>{item.title}</b></h6>
                                    <h6 style={{fontSize:"12px",fontWeight:"600",color:"#757575"}} className="card-text">{item.store}</h6>
                                    <h5><b className="card-text">$ {item.price}</b></h5>
                                   
                                    
                                    <hr/>
                                    <div className="row no-gutters">
                                        <div className="col-5">
                                        <a data-tip data-for='buynow' 
                                        style={{color:"#414361", cursor: 'pointer'}}  onClick={()=>this.handleAddtoCart(item)}>
                                        <i className="fa fa-shopping-cart fa-2x fa-fw"></i></a>
                                        <ReactTooltip id='buynow' type='dark'>
                                        <span>Add To Cart</span>
                                        </ReactTooltip>
                                        </div>   
                                        <div className="col-3">
                                        <div className="view zoom">    
                                        <span style={{color:"#414361",cursor: 'pointer'}} data-tip data-for='chat'
                                        onClick={()=>this.handleChat(item)} data-toggle="modal" data-target="#chatbot">
                                         <i className="fa fa-commenting fa-2x fa-fw"></i></span>
                                        <ReactTooltip id='chat' type='dark'>
                                        <span>Ask Niaomi !</span>
                                        </ReactTooltip>
                                        </div>
                                        </div> 
                                        <div className="col-1">
                                        </div>
                                        <div className="col-3">
                                        <span style={{color:"#414361",cursor: 'pointer'}} 
                                        data-toggle="modal" data-target="#sendto"
                                        data-tip data-for='send'
                                        onClick={()=>this.handleSendto(item)}>
                                        <i className="fa fa-share-alt fa-2x fa-fw"></i></span>
                                        <ReactTooltip id='send' type='dark'>
                                        <span>Share</span>
                                        </ReactTooltip>
                                        </div>  
                                              </div>    
                               
                                    </div> 
                                          
                            </div>
                            <div style={{display:(this.state.isopenanswerpanel?"":"none")}} >
                            <div className="card" key = {item.upid} style={{margin:'1vh',padding:"5px"}}>
                            <div>
                                <div className="text-center" style={{display:(this.state.isLoadingAnswer?"":"none")}}>    
                                <Spinner name="line-scale-pulse-out-rapid" color="orange"/>
                                </div>
                                <div className="text-center" style={{display:(!this.state.isLoadingAnswer?"":"none")}}>    
                                {this.handleAnswerSet(item.upid)}
                                </div>               
                             </div>
                             </div> 
                             </div>
                   </div>
                  

                  
                 
            
       )   
    }


    render() {
        
        return (
           <div>
           <AlertContainer ref={a => this.msg = a} {...this.alertOptions} />
           
                <Nav/>
                <br/>
                <Loading IsOn={this.state.isLoading}/>

                <div style={{
                    //marginLeft:"5vw",
                    marginTop:"6vh",
                    marginBottom:"0vh",

                   // marginRight:"5vw"
                   }} >
                 
                <div className="row" style={{paddingLeft:"3vw",paddingRight:"3vw" }}>
                    <div className="col-2">
                        <h5><b>{/*this.state.category.charAt(0).toUpperCase() + this.state.category.slice(1)*/}</b></h5>
                    </div>
                    <div className="col-2">

                    </div>
                    <div className="col-8">

                    

                        {/*<i className="fa fa-filter fa-2x fa-fw" data-tip data-for='filters'
                        aria-hidden="true" style={{cursor: 'pointer',float:'right',marginRight:'1vw'}} data-toggle="modal" data-target="#filters" ></i>
                        <ReactTooltip id='filters' type='dark' place="bottom">
                        <span>Filters</span>
                        </ReactTooltip>*/}
                        <span className="badge badge-pill teal" 
                        style={{cursor: 'pointer',float:'right',marginLeft:'-2vw',marginRight:'1vw'}} >{this.state.sendto.length}</span>
                        <i className="fa fa-share-alt fa-2x fa-fw" data-tip data-for='sendtofriend'
                        aria-hidden="true" style={{cursor: 'pointer',float:'right',marginRight:'2vw'}} data-toggle="modal" data-target="#sendto" ></i>
                        <ReactTooltip id='sendtofriend' type='dark' place="bottom">
                        <span>Send to Friends</span>
                        </ReactTooltip>
                        <i className="fa fa-question-circle fa-2x fa-fw" 
                        data-tip data-for='askicon' onClick={()=>{this.setState({isask: !this.state.isask,isopenanswerpanel:false})}}
                        aria-hidden="true" style={{cursor: 'pointer',float:'right',marginRight:'1vw'}}></i>
                        <ReactTooltip id='askicon' type='dark' place="bottom">
                        <span>Ask question to products</span>
                        </ReactTooltip>

                        <span className="badge badge-pill teal" 
                        style={{cursor: 'pointer',float:'right',marginLeft:'-2vw',marginRight:'1vw'}} >{this.state.cart.length}</span>
                        <i className="fa fa-shopping-cart fa-2x fa-fw" 
                        data-tip data-for='cart' data-toggle="modal" data-target="#cart"
                        aria-hidden="true" style={{cursor: 'pointer',float:'right',marginRight:'2vw'}}></i>
                        <ReactTooltip id='cart' type='dark' place="bottom">
                        <span>Shopping cart</span>
                        </ReactTooltip>
                            
                    </div>    

                </div>  
                
                <div className="row" style={{paddingLeft:"10vw",paddingRight:"10vw",marginTop:"1vh",display:(!this.state.isask)?"none":"",transition:"display 2s"}}>
                    <div className="col-8">
                    <div class="">
                            <input type="text" onKeyPress={this.handleAsk} ref="ask"
                            placeholder="Ask your question to products" class="form-control"/>
                            
                        </div>
                    </div>  
                    <div className="col-4">
                    <button className="btn btn-default btn-sm" onClick={()=>this.handleAskBtn()}>Ask</button>   
                    </div>      
                </div>
                <div id="productwindow"
                style={{paddingLeft:"3vw",paddingRight:"3vw",
                overflow:"scroll",height:"85vh"}}>
               
                <ReactCSSTransitionGroup
                    className="row"
                    transitionName="fade"
                    transitionEnterTimeout={3000}
                    transitionLeaveTimeout={3000}>
               

                {this.state.products.map(this.displayProducts,this)}
               
                 </ReactCSSTransitionGroup>
                 </div>
                {/* Model Product Description with Chatbot*/}
                <div className="modal fade right" id="chatbot" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-side modal-top-right" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <div className="row">
                                    <div className="col-2">
                                    <img src={this.state.chatproduct.image_url==""?require("../Images/notfound.png"):this.state.chatproduct.image_url} style={{}} className="img-fluid" alt={this.state.chatproduct.title}/>  
                                    </div>
                                    <div className="col-10">
                                    <h6 class="modal-title" id="exampleModalLabel">{this.isEmpty(this.state.chatproduct)==false?this.state.chatproduct.title:""}</h6>
                                    </div>          
                                    </div>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>

                                <div class="modal-body">
                                
                                   <PSM productid={this.state.selectedChatProductId} userid={localStorage.getItem("uuid")}/>
                                </div>
                                
                            </div>
                        </div>
                    </div> 
                </div> 
                {/* Send to Friends */}
                <div class="modal fade right" id="sendto" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-side modal-top-right" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title" id="exampleModalLabel">Send to friends</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                    <div style={{height:"60vh",overflow:"scroll"}}>

                                     
                                       {
                                        this.state.sendto.map((item,index)=>(
                                            <div>

                                                    <div className="row">
                                                        <div className="col-3">
                                                        <img style={{width:'100px',height: '50px',margin: '-75px 0 0 -100px'}}
                                                        src={item.image_url==""?require("../Images/notfound.png"):item.image_url} style={{}} className="img-fluid " alt={item.title}/>
                                                        </div>
                                                        <div className="col-7">
                                                        <h6 className="card-title">{item.title}</h6>
                                                        <h5><b className="card-text">$ {item.price}</b></h5>
                                                            <p>{}</p>
                                                        </div>   
                                                        <div className="col-1">
                                                            <i className="fa fa-trash fa-2x" onClick={()=>this.handleSendToDeleteItem(index)}
                                                            style={{cursor: 'pointer'}}></i>
                                                        </div>   
                                                    </div>
                                                    <hr/>
                                            </div>    
                                           

                                       ))
                                       }
                                       </div>
                                       <div className="md-form form-sm">
                                            <i className="fa fa-tag prefix"></i>
                                            <input type="text" id="collectionname" ref="collectionname" className="form-control form-control-sm"/>
                                            <label for="collectionname">Collection Name</label>
                                       </div>
                                       <div className="md-form form-sm">
                                            <i className="fa fa-comments prefix"></i>
                                            <input type="text" id="message" ref="msg" className="form-control form-control-sm"/>
                                            <label for="message">Message</label>
                                       </div>   
                                       <div className="row">
                                       <div className="col-6">    
                                        <div className="md-form form-sm">
                                            <i className="fa fa-phone prefix"></i>
                                            <input type="text" id="sendtomobno" ref="mobilenumbers" className="form-control form-control-sm"/>
                                            <label for="sendtomobno">Mobile Number</label>
                                       </div>
                                       </div>
                                       <div className="col-6">  
                                       <div className="md-form form-sm">
                                            <i className="fa fa-envelope prefix"></i>
                                            <input type="text" id="sendtoemail" ref="emails" className="form-control form-control-sm"/>
                                            <label for="sendtoemail">Email</label>
                                       </div>
                                       </div>
                                       </div>
                                       
                                       
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                   {/* <button type="button" class="btn btn-default" data-dismiss="modal">Clear</button>*/}
                                    <button type="button" data-dismiss="modal" class="btn btn-primary" onClick={()=>this.handleSendtoFriends()}
                                    disabled={(this.state.sendto.length>0)?"":"disabled"}>Send</button>
                                </div>
                            </div>
                        </div>
                </div>
                {/* Filters */}
                <div class="modal fade right" id="filters" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-side modal-top-right" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title" id="exampleModalLabel">Filters</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                            ...
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="button" class="btn btn-primary">Apply</button>
                                </div>
                            </div>
                        </div>
                
                </div>   
                {/* Quick View */}
                <div class="modal fade center" id="quickview" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-lg" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title" id="exampleModalLabel">Quick View</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                          ...
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="button" class="btn btn-primary">Apply</button>
                                </div>
                            </div>
                        </div>
                
                </div>  
                 {/* add to card */}
                 <div class="modal fade right" id="cart" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div class="modal-dialog modal-side modal-top-right" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title" id="exampleModalLabel">Shopping Cart</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                    <div style={{height:"70vh",overflow:"scroll"}}>

                                     
                                       {
                                        this.state.cart.map((item,index)=>(
                                            <div>

                                                    <div className="row">
                                                        <div className="col-3">
                                                        <img style={{width:'100px',height: '50px',margin: '-75px 0 0 -100px'}}
                                                        src={item.image_url==""?require("../Images/notfound.png"):item.image_url} style={{}} className="img-fluid " alt={item.title}/>
                                                        </div>
                                                        <div className="col-7">
                                                        <h6 className="card-title">{item.title}</h6>
                                                        <h5><b className="card-text">$ {item.price}</b></h5>
                                                            <p>{}</p>
                                                        </div>   
                                                        <div className="col-1">
                                                            <i className="fa fa-trash fa-2x" onClick={()=>this.handleAddToCartDeleteItem(index)}
                                                            style={{cursor: 'pointer'}}></i>
                                                        </div>   
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-4">
                                                         Qty:
                                                         <select class="form-control" 
                                                         onChange = {(e)=>this.handleupdatecart(index,"qty",e)}   id="sel1">
                                                            <option>1</option>
                                                            <option>2</option>
                                                            <option>3</option>
                                                            <option>4</option>
                                                            <option>5</option>
                                                            <option>6</option>
                                                            <option>7</option>
                                                            <option>8</option>
                                                            <option>9</option>
                                                            <option>10</option>
                                                            
                                                        </select>
                                                        </div>
                                                        <div className="col-4">
                                                        Color:<select onChange = {(e)=>this.handleupdatecart(index,"color",e)} 
                                                         class="form-control">
                                                             
                                                        {item.colors.map((color,index)=>{
                                                            if(color===""){
                                                                return <option>N/A</option>
                                                            }
                                                        
                                                             return <option>{color}</option>
                                                         } )}
                                                            </select>
                                                            
                                                        </div>   
                                                        <div className="col-4" >
                                                         Size:<br/>
                                                        <select onChange = {(e)=>this.handleupdatecart(index,"size",e)} 
                                                         class="form-control">
                                                        {item.sizes.map((size,index)=>{
                                                            if(size===""){
                                                                return <option>N/A</option>
                                                            }
                                                        
                                                             return <option>{size}</option>
                                                         } )}
                                
                                                       
                                                        </select>
                                                         
                                                        </div>   
                                                    </div>
                                                    <hr/>
                                            </div>    
                                           

                                       ))
                                       }
                                       </div>
                                        
                                     
                                       
                                       
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                   {/* <button type="button" class="btn btn-default" data-dismiss="modal">Clear</button>*/}
                                   <button class="btn btn-yellow" onClick={()=>this.handleCheckout("checkout")} data-dismiss="modal" >Checkout</button>
                                </div>
                            </div>
                        </div>
                 </div>
            </div>  
          
        );
    }
}

function mapStateToProps(state){
    
      return{
         
         products : state.products,
        // search : state.products
         
      }
  }
  function matchDispatchToProps(dispatch){
      
      return bindActionCreators(myactions,dispatch)
    
      
  }

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(Products));



