
import axios from 'axios';
import {Products} from '../constants/routes';

const headers = {
    'Accept': 'application/json'
};

export function getCollections(shareid,shareuuid){
   
    return  dispatch => {
       
     axios.get("http://orch.eastus.cloudapp.azure.com:8080/rating?shareid="+shareid)
     .then(function (response) {
       
          let  obj = {
              status : response.status,
              data : response.data
          }
          return dispatch({ 
              type : "SharedCollections_Result", 
              payload :obj
             } )
         
          })
        .catch(function (error) {
            return dispatch({ type : "Checkout_Error", payload : error } )
         });
         
     }
  } 

  export function feedback(data){
      
    return  dispatch => {
       
        axios.post("http://orch.eastus.cloudapp.azure.com:8080/rating",data)
         .then(function (response) {
            let obj= {
                status : response.status,
                data : response.data
              }
              return dispatch({ 
                  type : "Feedback_Result", 
                  payload : obj
                 })
             
              })
            .catch(function (error) {
                return dispatch({ type : "System_Error", payload : error } )
             });
          
        }
        
  } 


