var profile_initialstate ={
    
    signinStatus:0,
    verificationStatus:0
   
 }
 export default function(state=profile_initialstate,action){
     
     if(action.type === "SignIn_Result"){
         return{
             ...state,
             signinStatus:action.payload   
         }
     }
     if(action.type === "Verification_Result"){
        return{
            ...state,
            verificationStatus:action.payload   
        }
    }
 
     return state;
 }


