import React, {Component} from 'react';
import Slider from 'react-slick';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import { withRouter } from 'react-router-dom'
import * as myactions from '../action_creators/products';

function SampleNextArrow(props) {
    const {className, style, onClick} = props
    return (
      <div 
      className={className}
      style={{...style,width:"50px",height:"50px",padding:"5px",borderTopRightRadius:"50%",
      borderTopLeftRadius:"50%",borderBottomLeftRadius:"50%",
      borderBottomRightRadius:"50%", display: 'block', background: 'black'}}
      onClick={onClick}
      ></div>
    )
  }
  
function SamplePrevArrow(props) {
    const {className, style, onClick} = props
    return (
      <div
        className={className}
        style={{...style,width:"50px",height:"50px",
        padding:"5px",borderTopRightRadius:"50%",
        borderTopLeftRadius:"50%",borderBottomLeftRadius:"50%",borderBottomRightRadius:"50%", display: 'block', background: 'black'}}
        onClick={onClick}
      ></div>
    )
  }

class Sliderr extends React.Component {
    constructor(props){
        super(props);
        this.state = {
        
          category:[],
          type:props.type,
          route:"/products/",
          infinite:false

        }
     }
     componentDidMount() {
      this.props.onLoad(localStorage.getItem("uuid"));
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.products) {
          
          let data = nextProps.products.products[this.state.type];
          let category = [];
          for (var property in data) {
            if (data.hasOwnProperty(property)) {
                // do stuff
               
                var length = data[property].length;
                var img = "https://progressive-sports.co.uk/files/5414/0205/1331/multi-sports.png";
                if(length > 0){
                  if(data[property][0].image_url !=="" && data[property][0].image_url !== null)
                     var img = data[property][0].image_url;
                }
          
                let obj = {
                  title:property,
                  imageUrl:img
                }
                category.push(obj)
            }
          }
       
          if(category.length>4)
          this.setState({
           
            infinite:true
           
          })
          this.setState({
           
            category:category
           
          })
        }
    }
    render() {
    var settings = {
      autoplay:false,
      dots: true,
      infinite: this.state.infinite,
      speed: 500,
      swipe:true,
      swipeToSlide:true,
      slidesToShow: 4,
      slidesToScroll: 1,
      
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />,
      responsive: [{
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true
        }
      }, {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      }, {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1
        }
      }]
    };
    return (
      <div>  
      <Slider {...settings}>
        {console.log(this.state.category)}
        {this.state.category.map((item,index)=>(
          
            <div key={index} style={{padding:'5vh'}} >
  
            <div className="view overlay zoom" style={{cursor:"pointer"}}>
              
            {/*<img onClick={()=> this.props.history.push(this.state.route+this.state.type+"/"+item.title)}
            src={item.imageUrl} className="img-fluid " alt="zoom"/>*/}
            
            <img onClick={()=> this.props.history.push(this.state.route)}
            src={item.imageUrl} className="img-fluid " alt="zoom"/>
            

            </div>
            <br/>
            <p style={{cursor:"pointer", textAlign:"center"}} onClick={()=> this.props.history.push(this.state.route+this.state.type+"/"+item.title)}>{item.title}</p>
          
        </div>
        ))}
       
       



    
      </Slider>
      </div>
    );
  }
}
function mapStateToProps(state){
  
    return{
       
       products : state.products
       
    }
}
function matchDispatchToProps(dispatch){
    
    return bindActionCreators(myactions,dispatch)
  
    
}

export default withRouter(connect(mapStateToProps,matchDispatchToProps)(Sliderr));


