import React, { Component } from 'react';
import Slider from "react-slick";



class ViewCollection extends Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
      }
      next() {
        this.slider.slickNext();
      }
      previous() {
        this.slider.slickPrev();
      }

    isEmpty(obj) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }
    votes(item){
        
        if(!this.isEmpty(item.votes)){
            let username = Object.keys(item.votes.uservotes)
            var cmp = []
            if(username.length>0){
                return(<div>
                    <h6><b>Upvotes:</b> {item.votes.Y}</h6>
                    <h6><b>Comments:</b></h6>
                    {username.map((com,i)=><div><b style={{color:"green"}}>{com}:</b><p>{item.votes.uservotes[com].comment}</p></div>)}
                </div> 
             )}
                        
         
        }
       
        return <p></p>
        
        
    }
    display(){
        var settings = {
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: false,
            swipeToSlide: false,
           
         };
        
       
        
        return(
            <div>
            <Slider ref={c => (this.slider = c)} {...settings}>
                {this.props.data.map((item,index)=>
                
                    <div key={index} style={{marginTop:'0vh',width:"100%",height:"50vh",overflow:"scroll"}}>
                        
                        <div style={{float:"left",height:"100%"}}>
                            <div class="view overlay zoom" 
                            style={{width: '300px', height: '300px', 
                            overflow:"hidden",cursor:"pointer"}}>
                            <img style={{height: '300px',objectFit: "contain"}} src={item.info.image==""? require("../Images/notfound.png"):item.info.image}
                            className="img-fluid rounded " alt={item.title}/>
                            </div>
                        </div>   
                        <div>
                            <a href={item.info.url} target="_blank"
                            style={{cursor:"pointer", textAlign:"right"}}>{item.info.title}</a>
                            <p>{item.info.retailer}</p>
                            <h6><b>${item.info.price}</b></h6>
                         
                            {this.votes(item)}
                           
                            
                            
                         


                        </div>
                    </div>
                
                )}
            </Slider>
            <div style={{ textAlign: "center",marginTop:"2vh" }}>
                    <button className="btn btn-default" onClick={this.previous}>
                        Prev Item
                    </button>
                    <button className="btn btn-default" onClick={this.next}>
                        Next Item
                    </button>
            </div>
            </div>
        )
      
    }
    render() {
       
        return (
            <div className="card" style={{
                    maxWidth: '100%',
                    position: 'absolute',
                    left: '7%',
                    right: '7%',
                    margin: 'auto',
                    top: '50%',
                    transform: 'translateY(-50%)',
                    padding:'50px',
                    backgroundColor:''
                }}>
                <b>Collection Name</b>
                <hr/>
                {this.display()}

            </div>
        );
    }
}

export default ViewCollection;


