import axios from 'axios';
import {Products} from '../constants/routes';
import data from '../test.json';

const headers = {
    'Accept': 'application/json'
};
export function onSignIn(name,email,mobileno){
    return  dispatch => {

     let data = {
        "uphone":mobileno,
        "uname":name,
        "uemail":email
     }
     axios.post("http://projectxcauth.eastus.cloudapp.azure.com:8090/login",data)
     .then(function (response) {

          return dispatch({
              type : "SignIn_Result",
              payload : response.status
             } )

          })
        .catch(function (error) {
            return dispatch({ type : "Product_Error", payload : error } )
         });

     }
  }
  export function onVerification(name,email,mobileno,vcode){
    return  dispatch => {
    vcode = parseInt(vcode)
    let data = {
            "uphone":mobileno,
            "uname":name,
            "uemail":email,
            "vcode":vcode
         }
     console.log(data)
     axios.post("http://projectxcauth.eastus.cloudapp.azure.com:8090/login",data)
     .then(function (response) {

          return dispatch({
              type : "Verification_Result",
              payload : response
             } )

          })
        .catch(function (error) {
            return dispatch({ type : "Product_Error", payload : error } )
         });

     }
  }



