import React, {Component} from 'react';

import ReactImageMagnify from 'react-image-magnify'
import ReactSlick from 'react-slick';


import front_500 from '../Images/fashion1.jpeg'
import front_779 from '../Images/fashion1.jpeg'
import front_1020 from '../Images/fashion1.jpeg'
import front_1200 from '../Images/fashion1.jpeg'
import front_1426 from '../Images/fashion1.jpeg'

import back_500 from '../Images/fashion1.jpeg'
import back_779 from '../Images/fashion1.jpeg'
import back_1020 from '../Images/fashion1.jpeg'
import back_1200 from '../Images/fashion1.jpeg'
import back_1426 from '../Images/fashion1.jpeg'
const frontSrcSet = [
    { src: front_500, setting: '500w' },
    { src: front_779, setting: '779w' },
    { src: front_1020, setting: '1020w' },
    { src: front_1200, setting: '1200w' },
    { src: front_1426, setting: '1426w' }
]
    .map(item => `${item.src} ${item.setting}`)
    .join(', ');

const backSrcSet = [
    { src: back_500, setting: '500w' },
    { src: back_779, setting: '779w' },
    { src: back_1020, setting: '1020w' },
    { src: back_1200, setting: '1200w' },
    { src: back_1426, setting: '1426w' }
]
    .map(item => `${item.src} ${item.setting}`)
    .join(', ');

const dataSource = [
    {
        srcSet: frontSrcSet,
        small: front_500,
        large: front_1426
    },
    {
        srcSet: backSrcSet,
        small: back_500,
        large: back_1426
    }
];
class Images extends Component {
    constructor(props){
        super(props);
        this.state = {
          
        }
     }
   
    render() {
        return (
          <div style={{width:'300px',height:'300px'}}>
          <div className="fluid react-slick">
                <div className="fluid__image-container">
                    <ReactSlick {...{
                        dots: true,
                        infinite: true,
                        speed: 500,
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }}>
                        {dataSource.map((src, index) => (
                            <div key={index}>
                                <ReactImageMagnify {...{
                                    largeImage: {
                                        alt: '',
                                        src: src.large,
                                        width: 1426,
                                        height: 2000
                                    },
                                    smallImage: {
                                        isFluidWidth: true,
                                        alt: 'Wristwatch by Versace',
                                        src: src.small,
                                        srcSet: src.srcSet,
                                        sizes: '(max-width: 480px) 100vw, (max-width: 1200px) 30vw, 360px'
                                    },
                                    isHintEnabled: true,
                                    shouldHideHintAfterFirstActivation: false,
                                    enlargedImagePosition: 'over'
                                }} />
                            </div>
                        ))}
                    </ReactSlick>
                </div>
              
           
                <div style={{height: '1000px'}} />
            </div>
          </div>    
         
        );
    }
}



export default (Images);


