import axios from 'axios';
import {Products} from '../constants/routes';

const headers = {
    'Accept': 'application/json'
};
export function getCart(uuid){
    console.log(uuid)
    return  dispatch => {
       
     axios.get("http://projectxcauth.eastus.cloudapp.azure.com:8890/cart?uuid="+uuid)
     .then(function (response) {
       
          let  obj = {
              status : response.status,
              data : response.data
          }
          return dispatch({ 
              type : "Checkout_Result", 
              payload :obj
             } )
         
          })
        .catch(function (error) {
            return dispatch({ type : "Checkout_Error", payload : error } )
         });
         
     }
  } 


