import React, {Component} from 'react';
import {ThemeProvider}  from 'react-simple-chatbot';
import ChatBot from 'react-simple-chatbot';
//import Test from './test'
import TextChat from './chat/text'
class PSM extends Component {
  constructor(props){
      super(props);
      this.state = {
       productid :  props.productid,
       userid :  props.userid
      }
   }
   componentDidMount() {
    console.log("Prodid-PSM-Did"+this.state.productid)
   // this.handleEnd = this.handleEnd.bind(this);
  }


componentWillReceiveProps(nextProps) {
    this.setState({ productid: nextProps.productid });
  }
render() {
// all available props
const theme = {
  background: '#f5f8fb',
  fontFamily: 'Helvetica Neue',
  headerBgColor: '#EF6C00',
  headerFontColor: '#fff',
  headerFontSize: '15px',
  botBubbleColor: '#EF6C00',
  botFontColor: '#fff',
  userBubbleColor: '#fff',
  userFontColor: '#4a4a4a',

};
var index=0;
const steps = [
  {
    id: 'intro',
    message: 'Hello,Quick FAQ about product/retailer policies',
    trigger: 'question',
  },
  {
    id: 'question',
    user: true,
    trigger: 'answer',

  },
  {
    id: 'answer',
    component:<TextChat key={index++}productid={this.state.productid} userid={this.state.userid}/>,
    waitAction: true,
    asMessage:true,
    trigger: 'question',
    delay:1000

  }
];
return(

      <ChatBot handleEnd={this.handleEnd} headerTitle={"Personal Assistant"}
        recognitionEnable={true} steps={steps} />

)


}

}
export default PSM;



