import React, {Component} from 'react';
import { withRouter } from 'react-router-dom'


class Nav extends Component {
    constructor(props){
        super(props);
        this.state = {
          search:"",
          islogin:false
        }
     }

    componentDidMount(){
        if (typeof(Storage) !== "undefined") {
            if(localStorage.getItem('uuid')==null || localStorage.getItem('uuid')==undefined){
                    //this.props.history.push('/signin');
            }else{
                this.setState({islogin:true})
            }    
        }
            // Store
     }
    handlesearch(name, value){

     
       this.setState({
           search:value
       })
    }

    handlesearchKeyPress= (e) => {
        if (e.key === 'Enter') {
         // this.props.history.push("/?query="+this.state.search);
          window.open("/?query="+this.state.search, '_self');
        }
      
    }
    
    handleLogout(){
        localStorage.clear();
        this.setState({islogin:false})
        this.props.history.push('/signin');
    }
    render() {
        return (
           <div>
           <nav className="navbar navbar-expand-lg fixed-top navbar-dark mdb-color">


                    <a className="navbar-brand" href="/"><b><i>ugtme</i></b></a>

                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#basicExampleNav" aria-controls="basicExampleNav"
                        aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="basicExampleNav">


                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item active">
                                <a className="nav-link" href="/">Home
                                    <span className="sr-only">(current)</span>
                                </a>
                            </li>

                        </ul>
                        <input className="form-control" onChange={e => this.handlesearch('search',e.target.value)}
                         style={{width:"50vw"}}   onKeyPress={this.handlesearchKeyPress}
                         name="search" type="search" placeholder="Search" aria-label="Search"/>
                        {/*  
                        <div className="form-inline ml-auto">
                            <button className="btn btn-outline-white btn-md"
                            type="submit"><a href={"/products/search/"+this.state.search} style={{color:"white"}}>Search</a></button>
                        </div>
                         */} 
                       
                            
                       <ul className="nav navbar-nav nav-flex-icons ml-auto">
                            
                    
                            <li className="nav-item dropdown">
                                <a className="nav-link dropdown-toggle"
                                 href="#" id="navbarDropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <i className="fa fa-user"></i> <span class="clearfix d-none d-sm-inline-block"></span>
                                </a>
                                <div className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                    <a className="dropdown-item" href="/profile">Profile</a>
                                    <a className="dropdown-item" href="/collections">Collections</a>
                                    <a className="dropdown-item" style={{display:(this.state.islogin)?"":"none"}}
                                     onClick={()=>this.handleLogout()}>Logout</a>
                                </div>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/feedback"><i class="fa fa-envelope"></i> <span class="clearfix d-none d-sm-inline-block">Feedback</span></a>
                            </li>
                        </ul>
                        
                      {/*
                      <span class="navbar-text white-text">
                      <button className="btn btn-outline-white btn-md" >Logout</button>
                      </span>
                      */}
                    </div>







                </nav>
           </div>
        );
    }
}



export default withRouter(Nav);



