import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Home from './components/home'
import Products from './components/products'
import SignIn from './components/signin'
import SignUp from './components/signup'
import Contactus from './components/contactus'
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route } from 'react-router-dom';
//import createBrowserHistory from 'history/createBrowserHistory' 
import store from './store/index'
import Checkout from './components/checkout';
import contactus from './components/contactus';
import Collections from './components/collections';
import SharedCollections from './components/sharedcollections';
import collections from './components/collections';
import Feedback from './components/feedback';

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
        <div>
        <Route  exact path="/" component={Products}/>  
        <Route  path="/signin" component={SignIn}/>  
        <Route  path="/signup" component={SignUp}/> 
        <Route  path="/products/:type/:category" component={Products}/>
        <Route  path="/products" component={Products}/>  
        <Route  path="/checkout" component={Checkout}/>  
        <Route  path="/feedback" component={Feedback}/>  
        <Route  path="/profile" component={Contactus}/>  
        <Route  path="/collections" component={collections}/> 
        <Route  path="/sharedcollection" component={SharedCollections}/>  
              
        </div>
        
        </Router>
      </Provider>
    );
  }
}

export default App;



