import axios from 'axios';
import {Products} from '../constants/routes';
import data from '../test.json';

const headers = {
    'Accept': 'application/json'
};
export function onLoad(uuid,start,end){
    console.log(uuid)
    return  dispatch => {
       
     axios.get("http://orch.eastus.cloudapp.azure.com:8080/display?uuid="+uuid+"&start="+start+"&end="+end)
     .then(function (response) {
         // console.log(JSON.stringify(data))
         dispatch({ 
              type : "Products_Result", 
              payload : response.data
             } )
         
          })
          axios.get("http://projectxcauth.eastus.cloudapp.azure.com:8890/cart?uuid="+uuid)
          .then(function (response) {
            
               let  obj = {
                   status : response.status,
                   data : response.data
               }
               return dispatch({ 
                   type : "Load_Cart_Result", 
                   payload :obj
                  } )
              
               })
             .catch(function (error) {
                 return dispatch({ type : "System_Error", payload : error } )
         })
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
         });
         
     }
  }  
