export function Sendto(data){
        console.log("hiii"+JSON.stringify(data))
    return  dispatch => {
       
     axios.post("http://orch.eastus.cloudapp.azure.com:8080/send_friend",data)
     .then(function (response) {
        console.log(" Received: hiii"+(response.status))
          //console.log(" Received: hiii"+JSON.stringify(response.data))
          let obj = {
              status:response.status,
              msg : response.data.message
          }
          dispatch({ 
              type : "Sendto_Result", 
              payload :obj
             } )
         
          })
         
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
         });
         
     }
  }  


 

export function chatbot(question,productid,userid){   
    return  dispatch => {
    axios.get("http://orch.eastus.cloudapp.azure.com:8080/text?query="+question+"&upid="+productid)
    .then(function (response) {
        console.log("hiii"+JSON.stringify(response.data))
        return dispatch({ 
            type : "Chatbot_Result", 
            payload : response.data
            } )
        
        })
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
        });
        
    }

 
} 

export function Ask(data){  
    console.log(data) 
    return  dispatch => {
    axios.post("http://orch.eastus.cloudapp.azure.com:8080/text",data)
     .then(function (response) {
          let obj= {
            status : response.status,
            data : response.data
          }
          return dispatch({ 
              type : "Ask_Result", 
              payload : obj
             } )
         
          })
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
         });
      
        }
    
     
} 

export function cart(data){  
    return  dispatch => {
    console.log("Add to cart");    
    console.log(data);
    axios.post("http://projectxcauth.eastus.cloudapp.azure.com:8890/cart",data)
     .then(function (response) {
        let obj= {
            status : response.status,
            data : response.data
          }
          return dispatch({ 
              type : "Cart_Result", 
              payload : obj
             })
         
          })
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
         });
      
        }
    
     
} 


var context = this;


export function deletefromcollection(data,index){  
    return  dispatch => {
       
    console.log(data);
    axios.post("http://orch.eastus.cloudapp.azure.com:8080/delete",data)
     .then(function (response) {
        let obj= {
            status : response.status,
            data : response.data,
            index:index
          }
           dispatch({ 
              type : "Delete_Product_Result", 
              payload : obj
             })
         })   
        .catch(function (error) {
            return dispatch({ type : "System_Error", payload : error } )
         });
      
        }
    
     
}

